.navbar{
    display: flex;
    align-items: center;

    box-sizing: border-box;
    top: 0;
    width:100%;
    height: 350px;
    padding:0px 18px;
    display:inline-block;
    border-top: 1px solid #249bc7;
    background-color:#249bc7;
    text-align: center;
}

@media only screen and (min-width: 500px) {
    .navbar{
        display: flex;
        align-items: center;
    
        box-sizing: border-box;
        top: 0;
        width:100%;
        height: 250px;
        padding:0px 18px;
        display:inline-block;
        border-top: 1px solid #249bc7;
        background-color:#d4da83f3;
        text-align: center;
    }
  }

 h1 {
    padding-top: 30px;
    text-align: center;
    color: white;
    font-family: Futura, "Trebuchet MS", Arial, sans-serif; font-size: 40px; font-style: normal; font-variant: normal; font-weight: 700; line-height: 44px;
  }

  p {
    text-align: center;
    color: white;
    font-family: Arial, Arial, sans-serif; font-size: 20px; font-style: normal; font-variant: normal;
    padding-bottom: 10px;
}